<template>
         <div>
              <div class="all">
                  <div>维修人姓名：</div>
                  <div>{{item.realname}}</div> 
              </div>
              <div class="all">
                  <div>维修人手机：</div>
                  <div>{{item.telphone}}</div> 
              </div>
              <div v-if="item.repairtype==1" class="all">
                  <div>故障类型：</div>
                  <div style="margin-left:54px;">手机</div> 
              </div>
               <div v-if="item.repairtype==1" class="all">
                  <div>手机型号：</div>
                  <div  style="margin-left:54px;">{{item.brand}}</div> 
              </div>
              <div v-if="item.repairtype==2" class="all">
                  <div>故障类型：</div>
                  <div  style="margin-left:54px;">电脑</div> 
              </div>
              <div v-if="item.repairtype==3" class="all">
                  <div>故障类型：</div>
                  <div  style="margin-left:54px;">宽带</div> 
              </div>
              <div class="all">
                  <div>故障说明：</div>
                  <div style="margin-left:54px;">{{item.malfunction}}</div> 
              </div>
               <div class="all">
                  <div class="title">故障图片：</div>
                  <div  class="main"style="margin-left:-20px;">
                     <div   class="upload-list"v-for='value,index in img'>
                        <img   v-bind:src="value" alt="">
                        <div class="action-cover">
                            <dir class="action-cover"><Icon class="ico" type="ios-eye-outline"   @click.native="handleView(value)" title='查看大图'></Icon></dir>
                        </div>
                      </div>
                   </div>
            </div>
              <Modal title="预览" v-model="visible">
                  <img :src='viewSrc' v-if="viewSrc" style="width: 100%">
              </Modal>
                  </div>
               </div>
         </div>
</template>
<script>
export default {
  name:'repair-detail',
  data() {
    return {
      repairid:'',
      item:'',
      piclist:'',
      img:'',
      visible: false,
      viewSrc: '',
    }
  },
  mounted() {
    //获取维修详情id
    let repairid = this.$route.query.repairid || ''
    this.repairid = repairid
    console.log("repairid",repairid)
     this.$http.post(this.$api.repair_get, {repairid}).then(res => {
        if (res.data.result == 1 && res.data.data) {
          this.item = res.data.data;
           this.piclist=res.data.data.faultimg
          var str = this.piclist;
          var pic = str.split(",");
          console.log(pic)
          this.img=pic
          console.log("this.img",this.img)
          // var list = {};
          // for (var key in pic ) {
          //       list[key] = pic[key];
          //   }
          // console.log(list);
         } else {
          this.$Message.error(res.data.msg);
        }
      });
      
  },
    methods: {
       handleView(src) {
      this.viewSrc = src;
      this.visible = true;
    },
    }

}
</script>
<style>
.all{
  display: flex;
  flex-direction:row;
  margin: 20px;
  font-size: 14px;
  margin-bottom: 24px;
}
.all>div:nth-of-type(2){
  margin-left:40px; 
}
.orther{
  margin-left:50px; 
}
.title{
  width:126px 
}
.main{
  display: flex;
  flex-direction: row;
  flex-wrap:wrap;
}

.action-cover {
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, .6);
}

.show-area {
  width: 75px;
  height: 75px;
  line-height: 58px;
  display: inline-flex;
  align-items: center;
  justify-content: space-around;
  border: 1px dashed silver;
  border-radius: 5px;
  cursor: pointer;
}

.show-area:hover {
  border-color: #3bc0c3
}

.upload-list {
  display: inline-block;
  width: 75px;
  height: 75px;
  text-align: center;
  line-height: 75px;
  border: 1px solid transparent;
  border-radius: 4px;
  overflow: hidden;
  background: #fff;
  position: relative;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .2);
  margin-right: 34px;
}

.upload-list img {
  width: 100%;
  height: 100%;
}

.upload-list:hover .action-cover {
  display: block;
}

.action-cover i {
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  position: absolute;
  top:40%;
  left:40% ;
}
</style>



